<template>
  <div>
		<div>
			<ul style="text-align:left">
				<li></li>
				<li><v-btn></v-btn></li>
				<li><v-btn @click="get_dictation"></v-btn></li>
				<li><v-btn></v-btn></li>
			</ul>
		</div>
		<div>
			<ul style="text-align:left">
				<li></li>
				<li><input type="text" placeholder=" " style="border"></li>
			</ul>
		</div>
		<div>
			<ul>
				<li>/</li>
				<li>/</li>
				<li><a href=""></a></li>
				<li></li>
			</ul>
		</div>
		
		<div class='problem' v-for="(problem,i) of problems" :key="i"> 
			<div class='idx'>{{i + 1}}</div>
			<div class='problem-text' @keyup.enter="problemAdd"><input v-model="problem.dictationSentence" type="text" name="test_text" placeholder=" " maxlength="20"></div>
			<div class='problem-del' @click="problemDel(i)"><a><i class="far fa-minus-square fa-lg"></i></a></div>
			<voice-recorder :idx='i' @audio-change="audioChange" class="recorder"></voice-recorder>
		</div>
		
		<div class='problem-add' @click="problemAdd"><li><a><i class="far fa-plus-square fa-lg"></i></a></li></div>
		<v-btn class='problems-create' @click="createProblems"></v-btn>
  </div>
</template>

<script>
import VoiceRecorder from '@/components/VoiceRecord.vue'
import Axios from 'axios'
export default {
	name: 'classhwcreate',
	components:{VoiceRecorder: VoiceRecorder},
	data(){
		return {
			problems:[{dictationSentence:'', audioString: null, homeworkNo:4}]
		}
	},
	methods:{
		problemAdd(){
			this.problems.push({dictationSentence:'', audioString: null, homeworkNo:4})
		},
		problemDel(idx){
			this.problems.splice(idx,1)
		},
		audioChange(i,base64Data){
			this.problems[i].audioString = base64Data
		},
		createProblems(){
			Axios.post(`/dictation/dictation`,this.problems)
			.then(() => {
				console.dir('')
			})
			.catch(()=>{
				alert('error')
			})
		}
	}
}
</script>

<style scoped>
ul {
	list-style: none;
}
li {
	display: inline;
	text-align: -webkit-match-parent;
}
.problem{
	display: flex;
	justify-content: center;
	align-items: center;
}
.recorder{
	width:100px;
	height: 50px;
}
</style>
