<template>
  <v-container class="wrapper">
    <v-row>
      <v-col cols="8">
        <div>
          <!-- <v-img :src="$store.getters.BACKEND_URL + '/letter/' + koreanlist[idx].name + '.png'" aspect-ratio="1" class="koreanimg"></v-img> -->
          <alphabet-writing ref="canvas" v-if="flag" :letter="koreanlist[koreanno].name"></alphabet-writing>
          <!-- <article><span>{{koreanlist[this.koreanno].name}}</span></article> -->
          <v-img src="../../assets/images/whale.png" aspect-ratio="1" class="img-whale">
            <div class="whale-content">
              <h2>{{koreanlist[koreanno].text}}</h2>
            </div>
          </v-img>
        </div>
      </v-col>
    </v-row>
    <div class="move-btn">
      <template v-if="koreanno == 13 || koreanno == 23 || koreanno == 28 || koreanno == 39 || koreanno == 53">
        <v-btn @click="behind"></v-btn>
        <v-btn @click="finish"></v-btn>
      </template>
      <template v-else-if="koreanno < 53">
        <v-btn @click="behind"></v-btn>
        <v-btn @click="next"></v-btn>
      </template>
    </div>
  </v-container>
</template>

<script>
import AlphabetWriting from '@/components/AlphabetWriting'

export default {
  name: 'koreandetail',
  components:{'AlphabetWriting':AlphabetWriting},
  data(){
    return {
      flag: true,
      koreanno : 0,
      koreanlist: [
        { id:"0", name: '', text: '' }, 
        { id:"1", name: '', text: '' }, 
        { id:"2", name: '', text: '' }, 
        { id:"3", name: '', text: '' }, 
        { id:"4", name: '', text: '' }, 
        { id:"5", name: '', text: '' }, 
        { id:"6", name: '', text: '' }, 
        { id:"7", name: '', text: '' }, 
        { id:"8", name: '', text: '' }, 
        { id:"9", name: '', text: '' }, 
        { id:"10", name: '', text: '' }, 
        { id:"11", name: '', text: '' }, 
        { id:"12", name: '', text: '' }, 
        { id:"13", name: '', text: '' }, 
        { id:"14", name: '', text: '' }, 
        { id:"15", name: '', text: '' }, 
        { id:"16", name: '', text: '' }, 
        { id:"17", name: '', text: '' }, 
        { id:"18", name: '', text: '' }, 
        { id:"19", name: '', text: '' }, 
        { id:"20", name: '', text: '' }, 
        { id:"21", name: '', text: '' }, 
        { id:"22", name: '', text: '' }, 
        { id:"23", name: '', text: '' }, 
        { id:"24", name: '', text: '' }, 
        { id:"25", name: '', text: '' }, 
        { id:"26", name: '', text: '' }, 
        { id:"27", name: '', text: '' }, 
        { id:"28", name: '', text: '' }, 
        { id:"29", name: '', text: '' }, 
        { id:"30", name: '', text: '' }, 
        { id:"31", name: '', text: '' }, 
        { id:"32", name: '', text: '' }, 
        { id:"33", name: '', text: '' }, 
        { id:"34", name: '', text: '' }, 
        { id:"35", name: '', text: '' },         
        { id:"36", name: '', text: '' }, 
        { id:"37", name: '', text: '' }, 
        { id:"38", name: '', text: '' }, 
        { id:"39", name: '', text: '' },
        { id:"40", name: '', text: '' }, 
        { id:"41", name: '', text: '' }, 
        { id:"42", name: '', text: '' }, 
        { id:"43", name: '', text: '' }, 
        { id:"44", name: '', text: '' }, 
        { id:"45", name: '', text: '' }, 
        { id:"46", name: '', text: '' }, 
        { id:"47", name: '', text: '' }, 
        { id:"48", name: '', text: '' }, 
        { id:"49", name: '', text: '' }, 
        { id:"50", name: '', text: '' }, 
        { id:"51", name: '', text: '' }, 
        { id:"52", name: '', text: '' }, 
        { id:"53", name: '', text: '' }, 
      ],
    }
  },
  created(){
    this.koreanno = this.$route.params.koreanno
    for(let i in this.koreanlist){
      if(this.koreanlist[i].id == this.koreanno){
        this.koreanno = i
        break
      }
    }
  },
  methods: {
    recreate(){
      this.flag = false;
      let timerId = setInterval(()=>{
                    if(!this.$refs.canvas){
                        clearInterval(timerId)
                        this.flag = true;
                    }
                },100)
      
      
    },
    next() {
      this.koreanno = parseInt(this.koreanno) + parseInt(1)
      this.$router.push(`/koreandetail/${this.koreanno}`)
      this.recreate()
    },
    behind() {
      if(this.koreanno == 0){
        return;
      }
      if(this.koreanno == 14){
       return;
      }
      if(this.koreanno == 24){
       return;
      }
      if(this.koreanno == 29){
        return;
      }
      if(this.koreanno == 40){
       return;
      }
      this.koreanno = this.koreanno - 1
      this.recreate()
    },
    finish() {
      if(this.koreanno == 13){
        this.$router.push('/alphabet')
      }
      if(this.koreanno == 23){
        this.$router.push('/alphabet')
      }
      if(this.koreanno == 28){
        this.$router.push('/alphabet')
      }
      if(this.koreanno == 39){
        this.$router.push('/alphabet')
      }
      if(this.koreanno == 53){
        this.$router.push('/alphabet')
      }
    }
  }
}
</script>

<style scoped>
section {
  display: table-cell;
  box-sizing: border-box;
  margin: auto;
  width: 100%;
  height: 80%;
  background-color: cadetblue;
}
article {
  font-size: 50px;
}
span {
  font-size: 250px;
}

.koreanimg {
  width: 400px;
  height: 400px;
}

.img-whale{
  position: relative;                                                            
  width: 200px;
  height: 200px;
  background-size: cover;
}

.whale-content{
  position: absolute;
  top:60%;
  left:40%;
  transform: translate(-50%, -50%);                                                                   
  font-size:5rem;
  z-index: 2;
  text-align: center;
}
.wrapper{
  padding : 1vh auto !important;
}
.row{
  padding : auto !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.img-whale{
  margin: 1vh auto !important;
}
.move-btn{
  height: 8vh !important;
}

</style>