<template>
  <v-app>
		<div>			
			<h4> </h4>

			<!--    -->
			<p> </p>

			<v-card elevation="0">
				<v-container>
					<v-row dense>
						<v-col cols="6" v-for="(clazz, index) in classes" :key="index" style="padding:1%">
							<v-card>
								<div class="d-flex flex-no-wrap justify-space-between">
									<div>
										<v-card-title	class="headline" v-text="clazz.className"></v-card-title>
										<v-card-subtitle>
											<p> : {{ clazz.classDesc }}</p>
											<p> : {{ clazz.userLimit }}</p>
											<!--  ,    -->
											<p> : {{ clazz.targetAge }}</p>
											<p> : {{ clazz.startDate }} ~ {{ clazz.endDate }}</p>
											<p> : {{clazz.teacherName}}</p>
										</v-card-subtitle>
										<v-card-actions v-if="userInfo.userType == 'student'">
											<v-btn class="ml-2 mt-5" outlined rounded small>
												<class-inner :classNo="clazz.classNo"></class-inner>
												<!-- <router-link :to="'classinner/'+clazz.classNo"></router-link> -->
											</v-btn>
										</v-card-actions>
									</div>
									<v-avatar
										class="ma-3"
										size="50%"
										tile
									>
										<v-img :src="url + clazz.classImg" aspect-ratio="1" class="class_img">
											<!--   -->
											<p> {{ clazz.userNo }}</p>
										</v-img>
									</v-avatar>
								</div>
							</v-card>
						</v-col>
					</v-row>
				</v-container>
			</v-card>
		</div>
  </v-app>
</template>

<script>
import axios from 'axios'
import ClassInner from '@/components/Class/ClassInner.vue'
export default {
	name : 'Classsearch',
	components:{
		ClassInner
	},
	data() {
		return {
			classes : [],
		}
	},
	created() {
		axios.get('class/allclass')
		.then((Response)=>{
			this.classes = Response.data
		})
		.catch((error)=>{
			error
		})
	},
	computed : {
		url() {
			return this.$store.getters.BACKEND_URL
		},
		userInfo(){
			return this.$store.getters.userInfo
		}
	}
}
</script>

