        <div v-for="(classjoin,index) in wishjoinlist" :key="index">
            {{classjoin.userName}}({{classjoin.userId}})  : {{classjoin.birthDate}}<v-btn @click="acceptjoin(classjoin.studentNo)"></v-btn><v-btn @click="rejectjoin(classjoin.studentNo)"></v-btn>     
