<script>
import axios from 'axios'
export default {
    name : 'Classinner',
    props:['classNo'],
    data() {
        return {
            classInfo : [],
            check : 1, //1 , 2   , 3  , 4   
            show: false,
            wishjoinlist : [],
            memberlist : [],
        }
    },
    created() {
        axios.get(`class/oneclass/`+this.$route.params.classno)
        .then((Response)=>{
            this.classInfo = Response.data
            if(this.classInfo.userNo == this.userInfo.userNo){ //
                axios.get(`class/callwaitclass/`+this.$route.params.classno)
                .then((res)=>{
                    this.wishjoinlist = res.data
                    // console.dir(res.data)
                })
                .catch((err)=>{
                    err
                })

                axios.get(`class/memberlist/`+this.$route.params.classno) //  
                .then((res)=>{
                    this.memberlist = res.data
                    // console.dir(res.data)
                })
                .catch((err)=>{
                    err
                })
            }
        })
        .catch((error)=>{
            error
        })

        this.checkjoin()
    },
    methods : {
        outclass() {
            axios.delete(`class/outclass/${this.$route.params.classno}`)
            .then((Response)=>{
                Response
                alert('  .')
                this.$router.go()
            })
        },
        acceptjoin(userno) {
            let data = {
                classNo : this.$route.params.classno,
                studentNo : userno,
                isApproved : 1
            }
            axios.put('class/classjoin',data)
            .then((Response)=>{
                Response
                alert("  .");
            })
        },
        rejectjoin(userno){
            let data = {
                classNo : this.$route.params.classno,
                studentNo : userno,
                isApproved : 0
            }
            axios.put('/class/classjoin',data)
            .then((Response)=>{
                Response
                alert("  .");
            })
        },
        checkjoin(){
            axios.post('/class/checkjoin',{
                classNo : this.$route.params.classno,
                studentNo : this.userInfo.userNo
            })
            .then((Response)=>{
                this.check = Response.data
            })
        },
        joinclass() {
            axios.post('/class/joincall',{
                classNo : this.classNo
            })
            .then((Response)=>{
                Response
                alert('  .')
            })
        }
    },
    computed : {
        userInfo() {
            return this.$store.getters.userInfo
        },
        url() {
            return this.$store.getters.BACKEND_URL
        }
    }
}
</script>

