								<!-- ClassTimeTable.vue -->
								<v-tab title=""><router-link :to="'/timetable/'+this.$route.params.classno"></router-link></v-tab>
								<!-- ClassHomework.vue -->
								<v-tab title="" ><router-link :to="'/homework/'+this.$route.params.classno"></router-link></v-tab>
								<!-- ClassHwCreate.vue --> 
								<!--  ,   /    -->
								<v-tab title="" ><router-link :to="'/classhwcreate/'+this.$route.params.classno"></router-link></v-tab>
								<!-- ClassNotice.vue -->
								<v-tab title=""><router-link :to="'/notice/'+this.$route.params.classno"></router-link></v-tab>
								<!-- ClassStudents.vue -->
								<v-tab title=""><router-link :to="'/member/'+this.$route.params.classno"></router-link></v-tab>
								<!-- ClassJoinRequire.vue -->
