		</div>
	</v-app>
</template>

<script>
import NavBar from '@/components/NavBar.vue'
import FootBar from '@/components/FootBar.vue'
import axios from 'axios'

export default {
	name: 'classdetail',
	data(){
		return{
			state : 1,
			classInfo : []
		}
	},
	created(){
		this.get_classInfo();
	},
	components: {
		NavBar,
		FootBar,
	},
	methods: {
		get_classInfo(){
      axios.get(`class/oneclass/`+this.$route.params.classno)
        .then((Response)=>{
            this.classInfo = Response.data
        })
        .catch((error)=>{
            error
        })
    }
	}
}
</script>

